.mainpicsec{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    width: 60%;
    margin: auto;
    gap: 1rem;    
}
.headingpara{
    font-size: 1.3rem;
    /* border-bottom: 3px solid #7E1CFE; */
}

.headinglast{
    width: 30%;
    margin: auto;
    color: #7E1CFE;
}
.bnsnameemailbtn button{
    padding: 0.7rem 2rem;
    color: #fefeff;
    border: 1px solid #7E1CFE;
    border-radius: 1rem;  
    margin: 1rem 0.2rem; 
    cursor: pointer;
}
.bnsnameemailbtn {
    width: 25%;
    margin: auto ;
}
.notifybtn button{
color: white;
padding: 0.7rem 2rem;
border: 1px solid white;
background-color: #7E1CFE;
border-radius: 1rem; 
margin: 1rem 3rem; 
cursor: pointer;
}
.notifybtn{
width: 20%;
margin: auto;
}
.setfreelancebtn button{
    padding: 0.7rem 2rem;
    color: #fbfafc;
    border: 1px solid #7E1CFE;
    border-radius: 1rem; 
    margin: 1rem 0.7rem; 
    cursor: pointer;
}
.setfreelancebtn{
    width: 20%;
    margin: auto;
}

@media screen and (max-width:1100px) {
    .mainpicsec{
        grid-template-columns: repeat(2,1fr);  
    }
    .headinglast{
        width: 70%;
    }
    .setfreelancebtn button{ 
        margin: 0; 
    }
    .notifybtn button{
        margin: 1rem 5rem 0 0; 
        }
    
}
@media screen and (max-width:700px) {
    .mainpicsec{
        grid-template-columns: repeat(1,1fr);   
    }
    .headinglast{
        width: 70%;
    }
}