.login{
    margin: 0;
    position: absolute;
    top: 5%;
    left: 30%;
    background-color: rgb(23, 23, 73);
    padding: 4rem 6rem;
    box-shadow: 0 0 5px 2px rgb(12, 12, 12);
}
input{
    padding: 1rem 2rem;
    font-size: 1rem;
    border-radius: 1rem;
}
span{
    cursor: pointer;
}
label{
    font-size: 2rem;
    text-align: center;
    color: white;
}
.logincontainer{
    display: grid;
}
button{
    padding: 1rem 2rem;
    background-color: rgb(67, 67, 187);
    cursor: pointer;
    color: white;
    border-radius: 0.5rem;
}
.btnContainer p{
    font-size: 1.2rem;
    color: white;
}

@media screen and (max-width:1100px){
    .login{
        top: 5%;
        left: 20%;
        padding: 4rem 6rem;
    }
}
@media screen and (max-width:500px){
    .login{
        top: 5%;
        left: 5%;
        padding: 2rem 3rem;
    }
}