.HeadingMain{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.headintop{
    display: flex;
}
.Mainleftside{
grid-column: 1/3;
margin: 2rem 0 0 7rem;
}
.Mainrightside{
    grid-column: 3/5;
    margin-top: -5rem;
}
.headintop{
    font-size: 1.2rem;
}
.logoHeading{
    color: #7E1CFE;
    margin: 2rem 0 0 2rem;
}
.signuphead{
    color: #7E1CFE;
}
.buttonlast{
    display: flex;
    gap: 2rem;
}
.buttonone button{
color: white;
background-color:#7E1CFE;
padding: 0.6rem 2.5rem;
border-radius: 0.8rem;
cursor: pointer;
}
.buttontwo button{
color: rgb(25, 187, 216);
background-color: white;
padding: 0.6rem 2.5rem;
border-radius: 0.8rem;
cursor: pointer;
}
.blueundrline{
    text-decoration: underline;
    color: #7E1CFE;
}

  .effect-container{
position: relative;
overflow: hidden;

--effect-name:ripple;
--effect-duration: 1000ms;
--effect-top:0px;
--effect-left:0px;
--effect-height:100px;
--effect-width:100px;
--effect-color: white;
  }

  .effect-container::before{
    content: "";
    position: absolute;
    opacity: 0.3;

    top:var(--effect-top);
    left: var(--effect-left);
    height: var(--effect-height);
    width: var(--effect-width);
    border-radius: 50%;
    transform: scale(0);
    background:var(--effect-color);
  }
  .effect-container.active::before{
    border-radius: 50%;
    animation: ripple var(--effect-duration) linear forwards;
  }
  @keyframes ripple{
    from{
        transform: scale(0);
    }
    to{
     transform: scale(6);
     opacity: 0;
    }
  }

  @media screen and (max-width:1100px) {
    .HeadingMain{
        display: grid;
        grid-template-columns: 1fr;
    }
    .Mainleftside{
        grid-column: 1/2;
        margin: 2rem 0 0 7rem;
        }
        .Mainrightside{
            grid-column: 1/2;
            margin:0 0 0 3rem;
        }
  }
  @media screen and (max-width:700px) {
    .Mainleftside{
        grid-column: 1/2;
        margin: 0;
        }
  }