.topfooter{
display: grid;
grid-template-columns: repeat(3,1fr);
background-color: #D0D0D0;
height: 17rem;
}
.lefttop{
grid-column: 1/2;
}
.lefttop h3{
    margin: 1rem 2rem;
}
.centertop{
    grid-column: 2/3;
    margin-top: 6.4rem;
}
.righttop{
    grid-column: 3/4;
    margin-top: 2rem;
}
.rigthicons{
    font-size: 1.4rem;
}
.bottomfooter{
    display: grid;
    grid-template-columns: repeat(3,1fr);   
}
.leftbottom{
    grid-column: 1/2;
    margin: 2rem;
}
.rightbottom{
    grid-column: 3/4;
    margin: 2rem;
}

.bottomfooter{
    background-color: black;
    color: white;
    height: 6rem;
}