h3{
font-size: 1.5rem;
}
p{
font-size: 0.8rem;
}
.mainsec{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    margin: 3rem 0 0 3rem ;
}
.firstsec{
    box-shadow: 0 0 6px 6px  rgb(212, 209, 209);
    border-radius: 8px;
    padding: 2rem 2rem;
    width: 14rem;
}
.secsec{
    box-shadow: 0 0 6px 6px  rgb(212, 209, 209);
    border-radius: 8px;
    padding: 2rem 2rem;
    width: 14rem;
}
.thirdsec{
    box-shadow: 0 0 6px 6px  rgb(212, 209, 209);
    border-radius: 8px;
    padding: 2rem 2rem;
    width: 14rem;
}
.fourthsec{
    box-shadow: 0 0 6px 6px  rgb(212, 209, 209);
    border-radius: 8px;
    padding: 2rem 2rem;
    width: 14rem;
    margin-top: 2rem;
}
.fifthsec{
    box-shadow: 0 0 6px 6px  rgb(212, 209, 209);
    border-radius: 8px;
    padding: 2rem 2rem;
    width: 14rem;
    margin-top: 2rem;
}
.seclogo{
    font-size: 4rem;
    text-align: center;
    color: #7E1CFE;
}

@media screen and (max-width:900px) {
    .mainsec{
        grid-template-columns: repeat(2,1fr);
    }
    .thirdsec{
        margin-top: 2rem;
    }
    .secsec{
        margin-top: 2rem;
    }
    
}
@media screen and (max-width:650px) {
    .mainsec{
        grid-template-columns: 1fr;
    }
}

.lastsec{
    width: 40%;
    margin: auto;
    padding: 1rem;
}
.botfirst{
    margin-top: 3rem;
}
.lastsec h3{
    color: #7E1CFE;
}
